@use '../abstracts' as *;

.about {
    background-color: $beige;
    max-width: 100%;
    height: 100%;
    display: flex;
    align-items: center;    
    justify-content: center;

    &__text {
    max-width: 50%;
    padding: 3em;

        @media screen and (max-width: 320px) {
            max-width: 90%;
            padding: 1em;

        }
    }

    &__heading {
        @include font-bold; 
    }

    &__line {
        border-bottom: 1px solid $black;
        width: 80px;
    }

    &__para {
        @include font-normal;
    }
       
}   